<script>
export default {
	data(){
		return{
			address:"",
			dialogVisible:false,
			Connect_dialogTableVisible:false,
			lang:"EN",
			key:0,
			ref:""
		}
	},
	created() {
		var temp = this
		this.wallet.init()
		setInterval(function(){
			temp.address = temp.wallet.address
		},500)
		if(this.$route.query.ref != null && this.$route.query.ref.length == 42){
			this.ref = this.$route.query.ref
		}
		this.language.lang = this.$cookies.get("T3_language")
		if(this.language.lang=="zh"){
			this.lang = "中国"
		}else{
			this.language.lang = "en"
			this.lang = "EN"
		}
	},
	methods:{
		handleCommand(command) {
			if(command=="zh"){
				this.lang = "中国"
				this.language.lang = "zh"
				this.$cookies.set("T3_language","zh",Date.parse(new Date())/1000+60*60*24*90)
			}else if(command=="en"){
				this.lang = "EN"
				this.language.lang = "en"
				this.$cookies.set("T3_language","en",Date.parse(new Date())/1000+60*60*24*90)
			}
			this.key += 1
			//location.reload()
		},
		connectMetamask_click(){
			this.Connect_dialogTableVisible = false
			this.wallet.Metamask_Connect()
		},
		copyText(text, callback){
			  var tag = document.createElement('input');
			  tag.setAttribute('id', 'cp_hgz_input');
			  tag.value = text;
			  document.getElementsByTagName('body')[0].appendChild(tag);
			  document.getElementById('cp_hgz_input').select();
			  document.execCommand('copy');
			  document.getElementById('cp_hgz_input').remove();
			  if(callback) {callback(text)}
		},
		browserView(){
			window.open("https://hecoinfo.com/address/"+this.wallet.address);
		},
		copyAddress(){
			var temp = this
			this.copyText(this.wallet.address,function(){
				temp.$notify({
				  title: temp.language[temp.language.lang].success,
				  message: temp.language[temp.language.lang].Copy_address,
				  type: 'success'
				});
			})
		},
		but_wallet(){
			this.dialogVisible = true
		}
	}
}
</script>

