<template>
	<el-row type="flex" justify="center">
		<el-row style="width: 393px;">
			<el-tabs v-model="activeName" @tab-click="handleClick" :stretch="true">
				<el-tab-pane :label="language[language.lang].My_pledge" name="mymining">
					<el-card v-for="list in lists" shadow="always">
						<el-row type="flex" align="middle">
							<el-col :span="12">
								<el-row>
									<el-row type="flex" align="middle" style="font-size: 12px;white-space:nowrap;">
										{{language[language.lang].Pledge_amount}}：{{list.amount}}
										<img v-if="list.token=='T3'" style="margin-left: 5px;" width="12px" height="12px" src="../assets/t3c_12_12.png" />
										<img v-if="list.token=='HT'" style="margin-left: 5px;" width="12px" height="12px" src="../assets/ht_12_12.png" />
									</el-row>
									<el-row type="flex" align="middle" style="font-size: 12px;white-space:nowrap;">
										{{language[language.lang].Available_income}}：{{list.income}}
										<img style="margin-left: 5px;" width="12px" height="12px" src="../assets/t3c_12_12.png" />
									</el-row>
									<el-row type="flex" style="font-size: 12px;white-space:nowrap;">
										{{language[language.lang].Creation_time}}：{{data(list.creationTime*1000)}}
									</el-row>
									<el-row type="flex" style="font-size: 12px;white-space:nowrap;">
										{{language[language.lang].Release_time}}：{{data(list.releaseTime*1000)}}
									</el-row>
								</el-row>
							</el-col>
							<el-col :span="12">
								<el-row>
									<el-row v-if="Date.parse(new Date())/1000 > list.releaseTime" type="flex" justify="end">
										<el-row>
											<el-row  style="margin: 10px;font-weight:bold;color: #FF0000;" >{{language[language.lang].Pledge_over}}</el-row>
										</el-row>
									</el-row>
									<el-row v-if="Date.parse(new Date())/1000 < list.releaseTime" type="flex" justify="end">
										<el-row>
											<el-row style="margin: 10px;font-weight:bold;color: #008000;">
												{{language[language.lang].Pledge_in_progress}}
											</el-row>
											<el-button @click="but_makeProfit_click(list)" type="warning" round>{{language[language.lang].withdrawal}}</el-button>
										</el-row>
									</el-row>
								</el-row>
							</el-col>
						</el-row>
					</el-card>
				</el-tab-pane>
				<el-tab-pane :label="language[language.lang].Pledge_mining" name="buymining">
					<el-card v-for="list in lists2" shadow="always">
						<el-row @click.native="choice_click(list.id)" type="flex" align="middle">
							<el-row type="flex">
								<img v-if="list.token == 'T3'" width="40px" height="40px" src="../assets/t3c.png" />
								<img v-if="list.token == 'HT'" width="40px" height="40px" src="../assets/ht.png" />
							</el-row>
							<el-row style="margin-left: 8px;">
								<el-row type="flex" style="font-size: 18px;font-weight:bold;">{{list.token}}</el-row>
								<el-row type="flex" style="word-break: keep-all;white-space: nowrap;font-size: 12px;color:#696969;">{{list.explain}}</el-row>
							</el-row>
							<el-row v-if="choice != list.id" style="width: 100%;">
								<el-row type="flex" justify="end">
									<svg t="1629813216910" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="6505" width="20" height="20"><path d="M560.429 777.257c-14.014 14.013-33.032 27.026-51.05 23.022-18.017 3.003-35.034-11.01-49.047-23.022L26.912 341.835c-22.02-22.021-22.02-58.056 0-80.077s58.057-22.022 80.078 0l403.39 414.4 404.392-414.4c22.021-22.022 58.056-22.022 80.077 0s22.022 58.056 0 80.077L560.43 777.257z" p-id="6506" fill="#696969"></path></svg>
								</el-row>
							</el-row>
							<el-row v-if="choice == list.id" style="width: 100%;">
								<el-row type="flex" justify="end">
									<svg t="1629813632949" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="7000" width="20" height="20"><path d="M451.47303655 253.29604336c13.66737884-13.66737884 32.21596439-26.35851634 49.78830864-22.45355098 17.57234421-2.92872403 34.1684471 10.73865482 47.83582596 22.45355098L971.8096739 677.9610288c21.4773096 21.4773096 21.4773096 56.62199807 0 78.09930765s-56.62199807 21.4773096-78.09930766-1e-8l-393.42526241-404.16391718L105.88360012 756.06033644c-21.4773096 21.4773096-56.62199807 21.4773096-78.09930765 0s-21.4773096-56.62199807 0-78.09930763L451.47303655 253.29604336z" p-id="7001" fill="#696969"></path></svg>
								</el-row>
							</el-row>
						</el-row>
						<el-row type="flex" align="middle" style="margin-top: 10px;">
							<el-row type="flex" style="word-break: keep-all;white-space: nowrap;color:#696969;">
								{{language[language.lang].apr}}
							</el-row>
							<el-row type="flex" justify="end" style="width: 100%;font-weight:bold;color: #008000;">
								{{list.apr}}%
							</el-row>
						</el-row>
						<el-row type="flex" align="middle" style="margin-top: 10px;">
							<el-row type="flex" style="word-break: keep-all;white-space: nowrap;color:#696969;">
								{{language[language.lang].lock}}({{list.token}})
							</el-row>
							<el-row type="flex" justify="end" style="width: 100%;font-weight:bold;color: #000000;">
								{{list.lock}}
							</el-row>
						</el-row>
						<el-row type="flex" align="middle" style="margin-top: 10px;">
							<el-row type="flex" style="word-break: keep-all;white-space: nowrap;color:#696969;">
								{{language[language.lang].Lock_duration}}({{language[language.lang].day}})
							</el-row>
							<el-row type="flex" justify="end" style="width: 100%;font-weight:bold;color: #000000;">
								{{list.day}}
							</el-row>
						</el-row>
						<el-row type="flex" align="middle" style="margin-top: 10px;">
							<el-row type="flex" style="word-break: keep-all;white-space: nowrap;color:#696969;">
								{{language[language.lang].Remaining}}
							</el-row>
							<el-row type="flex" justify="end" style="width: 100%;font-weight:bold;color: #000000;">
								{{list.total-list.lock}} {{list.token}}
							</el-row>
						</el-row>
						<el-row type="flex" align="middle" style="margin-top: 10px;">
							<el-progress style="width: 100%;" :percentage="parseInt((list.total-list.lock)/list.total*100)"></el-progress>
						</el-row>
						<el-row v-if="choice == list.id">
							<el-divider></el-divider>
							<el-row type="flex" justify="end" align="middle" style="color:#696969;font-size: 12px;">
								<span v-if="list.token=='HT'">balance: {{ht_balance}}</span>
								<span v-if="list.token=='T3'">balance: {{t3_balance}}</span>
								<img v-if="list.token=='T3'" style="margin-left: 5px;" width="12px" height="12px" src="../assets/t3c_12_12.png" />
								<img v-if="list.token=='HT'" style="margin-left: 5px;" width="12px" height="12px" src="../assets/ht_12_12.png" />
							</el-row>
							<el-row class="mkfjg" style="margin-top: 10px;">
								<el-input v-model="input" placeholder="0"></el-input>
							</el-row>
							<el-row style="margin-top: 10px;">
								<el-button v-if="t3_allowance == 0 && list.token=='T3'" @click="approve" style="width: 80%;font-weight:bold;" type="primary" round>{{language[language.lang].Approve}}</el-button>
								<el-button v-if="t3_allowance != 0 || list.token=='HT'" @click="but_pledge_click(list)" style="width: 80%;font-weight:bold;" type="primary" round>{{language[language.lang].Pledge}}</el-button>
							</el-row>
						</el-row>
					</el-card>
				</el-tab-pane>
			</el-tabs>
		</el-row>
	</el-row>
</template>

<script>
	import Web3 from "web3";
	import $ from 'jquery'
	export default {
		data() {
			return {
				input:"",
				activeName: 'mymining',
				lists:[
				],
				lists2:[],
				choice:-1,
				ht_balance:0,
				t3_balance:0,
				t3_allowance:0,
				run:0
			}
		},
		created() {
			var temp = this
			var tim = Date.parse(new Date())/1000
			setInterval(function(){
				if(temp.wallet.address.length == 42){
					if(tim+2<Date.parse(new Date())/1000){
						temp.wallet.balanceOf("0x0000000000000000000000000000000000000000",temp.wallet.address,function(ret){
							temp.ht_balance = Math.floor(ret * 1000000) / 1000000
						})
						temp.wallet.balanceOf("0xB141503a1189d3Af7060F3F14Ac7D37d3d70348E",temp.wallet.address,function(ret){
							temp.t3_balance = Math.floor(ret * 1000000) / 1000000
						})
						temp.wallet.allowance(temp.wallet.address,"0x2BA4616a9c2144AF625edaAe4A6127503Ce85e2B","0xB141503a1189d3Af7060F3F14Ac7D37d3d70348E",function(result){
							temp.t3_allowance = result
						})
						tim = Date.parse(new Date())/1000
					}
					if(temp.run == 0){
						temp.wallet.pools({fromAddress:temp.wallet.address},function(result){
							console.log(result)
							for(var i=0;i<result.length;i++){
								var teb = {}
								teb.id = i
								teb.amount = result[i].amount/1000000000000000000
								if(result[i].token == "HT"){
									teb.amount=teb.amount/12000
								}
								teb.creationTime = result[i].creationTime
								teb.releaseTime = result[i].releaseTime
								teb.withdrawTime = result[i].withdrawTime
								var second = Date.parse(new Date())/1000 - result[i].withdrawTime;
								teb.income = parseFloat((second * (result[i].apr * result[i].amount)/10000) / 31536000/1000000000000000000).toFixed(2);
								teb.token = result[i].token
								teb.apr = result[i].apr/100
								temp.lists.push(teb)
							}
						})
						temp.run = temp.run + 1
					}
				}
				if(temp.wallet.address.length != 42){
					temp.ht_balance="-"
					temp.t3_balance="-"
				}
			},300)
			this.wallet.pledgeList(function(result){
				for(var i=0;i<result.length;i++){
					var teb = {}
					teb.id = i
					teb.explain = result[i].explain
					teb.day = result[i].day
					teb.total = result[i].total
					teb.lock = result[i].lock
					teb.token = result[i].token
					teb.apr = result[i].apr/100
					temp.lists2.push(teb)
				}
			})
		},
		methods: {
			data(time){
				var myDate = new Date(time)
				return myDate.getFullYear()+"/"+(myDate.getMonth()+1)+"/"+myDate.getDate()+" "+myDate.getHours()+":"+myDate.getMinutes()+":"+myDate.getSeconds()
			},
			but_pledge_click(tab){
				//console.log(JSON.stringify(tab))
				if(tab.token == "T3"){
					if(this.t3_balance >= this.input){
						this.wallet.pledgeT3({value:this.input,fromAddress:this.wallet.address},tab.id)
					}else{
						this.$notify({
							title: this.language[this.language.lang].Tips,
							message: this.language[this.language.lang].Your_wallet_doesnt_have_enough_balance,
							type: 'warning'
						});
					}
				}else{
					if(this.ht_balance-0.001 >= this.input){
						this.wallet.pledgeETH({value:this.input,fromAddress:this.wallet.address},tab.id)
					}else{
						this.$notify({
							title: this.language[this.language.lang].Tips,
							message: this.language[this.language.lang].Your_wallet_doesnt_have_enough_balance,
							type: 'warning'
						});
					}
				}
			},
			but_makeProfit_click(tab){
				this.wallet.takeProceeds({fromAddress:this.wallet.address},tab.id)
			},
			handleClick(tab, event) {
				
			},
			formatDecimal(num, decimal) {
				num = num.toString();
				var index = num.indexOf('.');
				if (index !== -1) {
					num = num.substring(0, decimal + index + 1)
				} else {
					num = num.substring(0)
				}
				return parseFloat(num).toFixed(decimal)
			},
			choice_click(id){
				this.input = ""
				if(this.choice == id){
					this.choice = -1
				}else{
					this.choice = id
				}
			},
			approve(){
				this.wallet.approve({fromAddress:this.wallet.address})
			}
		}
	}
</script>

<style>
	.mkfjg>.el-input>.el-input__inner{
		border-radius: 20px;
		font-size: 26px;
		color: #000000;
	}
</style>
