<script>
	import { Loading } from 'element-ui';
	export default {
		data(){
			return{
				partake:"0",
				balance:"0",
				participant:"0",
				total:"0"
			}
		},
		created() {
			var temp = this
			setInterval(function(){
				if(temp.wallet.address.length == 42){
					temp.wallet.user(temp.wallet.address,function(result){
						console.log(result)
						temp.partake = result.partake
						temp.balance = result.balance
						temp.participant = result.participant
						temp.total = result.total
					})
				}
				if(temp.wallet.address.length != 42){
					temp.partake="0"
					temp.balance="0"
					temp.participant="0"
					temp.total="0"
				}
			},1000)
		},
		methods:{
			Submit(){
				
			},
			but_Airdrop(){
				if(this.wallet.address.length!=42){
					this.$notify({
					  title: this.language[this.language.lang].Tips,
					  message: this.language[this.language.lang].Please_connect_a_wallet_first,
					  type: 'warning'
					});
					return
				}
				if(this.$route.query.ref == null){
					this.wallet.partake({fromAddress:this.wallet.address},"0x0000000000000000000000000000000000000000")
				}else if(this.$route.query.ref != null && this.$route.query.ref.length == 42){
					this.wallet.partake({fromAddress:this.wallet.address},this.$route.query.ref)
				}
			},
			but_withdraw(){
				this.wallet.withdraw({fromAddress:this.wallet.address})
			}
		}
	}
</script>

