<template>
	<el-row type="flex" justify="center">
		<el-dialog title="Connect to a wallet" :visible.sync="Connect_dialogTableVisible" width="380px">
			<el-row @click.native="connectMetamask_click()" type="flex" class="H02gS3">
				<el-row type="flex" align="middle"
					style="width:200px;font-size: 1rem;flex-flow: row nowrap;font-weight: 500;">
					Metamask
				</el-row>
				<el-row type="flex" justify="end" align="middle" style="width: 100%;">
					<img src="../assets/metamask.png" style="height: 36px;width: 36px;">
				</el-row>
			</el-row>
		</el-dialog>
		<el-dialog :title="language[language.lang].select_token" :visible.sync="dialogVisible" width="380px">
			<el-row>
				<el-row>
					<el-input :placeholder="language[language.lang].Enter_the_token_name_or_contract_address" v-model="search_input">
						<i slot="prefix" class="el-input__icon el-icon-search"></i>
					</el-input>
				</el-row>
				<el-card shadow="never" style="margin-top: 5px;height: 350px;overflow:auto;">
					<el-row v-for="item in token_items">
						<button @click="choiceToken(item.symbol)" style="background-color: rgb(255, 255, 255);border: none;width: 100%;">
							<el-row type="flex" align="middle">
								<img height="30" width="30" :src="item.logoURI" />
								<span style="font-size: 20px;">{{item.symbol}}</span>
							</el-row>
						</button>
						<el-divider v-if="token_items[token_items.length-1].symbol != item.symbol"></el-divider>
					</el-row>
				</el-card>
			</el-row>
		</el-dialog>
		<el-row style="width: 393px;">
			<el-card shadow="always">
				<el-row type="flex" align="middle"
					style="border: 1px solid rgb(235, 237, 243);padding: 10px;border-radius: 10px;">
					<el-col :span="12">
						<el-row>
							<el-input class="CSs54f8" v-model="from.input" @input="from_input" placeholder="0.0"></el-input>
						</el-row>
						<el-row type="flex">
							<b style="font-size: 12px;color: rgb(44, 114, 244);">Balance: {{from.balance}}</b>
						</el-row>
					</el-col>
					<el-col :span="5">
						<button  @click="max" style="background-color: rgb(255, 255, 255);border: none;">
							<el-tag>MAX</el-tag>
						</button>
					</el-col>
					<el-col :span="7">
						<button @click="choiceTokenPage('from')" style="background-color: rgb(255, 255, 255);border: none;">
							<el-row type="flex" align="middle">
								<img height="30" width="30" :src="from.logoURI" />
								<span style="font-size: 20px;">{{from.symbol}}</span>
								<svg width="12" height="7" viewBox="0 0 12 7" fill="none" color="#48587B"
									class="sc-iujRgT iEdtih">
									<path d="M0.97168 1L6.20532 6L11.439 1" stroke="#AEAEAE"></path>
								</svg>
							</el-row>
						</button>
					</el-col>
				</el-row>
				<el-row style="margin: 10px;">
					<button  @click="fe" style="background-color: rgb(255, 255, 255);border: none;">
						<svg t="1628083061354" class="icon" viewBox="0 0 1024 1024" version="1.1"
							xmlns="http://www.w3.org/2000/svg" p-id="2690" width="32" height="32">
							<path
								d="M514 65.5c-246.5 0-446.3 199.8-446.3 446.3S267.5 958.1 514 958.1s446.3-199.8 446.3-446.3S760.5 65.5 514 65.5zM407.3 752l-26.1 79.8-152.6-170.3 223.7-47-23 70.3c3.8 1.4 7.6 2.8 11.5 4 132.2 43.3 272.5-6.2 350.5-112.6-62.7 147.7-228.7 225.5-384 175.8z m169.4-334.7l16.7-71.8c-3.9-1.1-7.8-2.1-11.8-3-135.5-31.5-270.9 30.1-339.2 143 49.5-152.5 208-244.6 367-208.8l19.1-82 166.9 156.1-218.7 66.5z"
								fill="#409EFF" p-id="2691"></path>
						</svg>
					</button>
				</el-row>
				<el-row type="flex" align="middle"
					style="border: 1px solid rgb(235, 237, 243);padding: 10px;border-radius: 10px;">
					<el-col :span="12">
						<el-row>
							<el-input class="CSs54f8" v-model="to.input" @input="to_input" placeholder="0.0"></el-input>
						</el-row>
						<el-row type="flex">
							<b style="font-size: 12px;color: rgb(44, 114, 244);">Balance: {{to.balance}}</b>
						</el-row>
					</el-col>
					<el-col :span="5">

					</el-col>
					<el-col :span="7">
						<button @click="choiceTokenPage('to')" style="background-color: rgb(255, 255, 255);border: none;">
							<el-row type="flex" align="middle">
								<img height="30" width="30" :src="to.logoURI" />
								<span style="font-size: 20px;">{{to.symbol}}</span>
								<svg width="12" height="7" viewBox="0 0 12 7" fill="none" color="#48587B"
									class="sc-iujRgT iEdtih">
									<path d="M0.97168 1L6.20532 6L11.439 1" stroke="#AEAEAE"></path>
								</svg>
							</el-row>
						</button>
					</el-col>
				</el-row>
				<el-row style="margin-top: 10px;margin-bottom: 10px;width: 100%;">
					<el-button v-if="this.wallet.address.length != 42" @click="Connect_dialogTableVisible = true" type="primary" style="width: 100%;font-weight:bold;" >{{language[language.lang].connect_Wallet}}</el-button>
					<el-button v-if="this.wallet.address.length == 42" :disabled='exchange_disabled' @click="transact" type="primary" style="width: 100%;font-weight:bold;" >{{language[language.lang].exchange}}</el-button>
				</el-row>
			</el-card>
		</el-row>
	</el-row>
</template>
//npm install @mdex/heco-sdk

<script>
	import Web3 from "web3";
	import vConsole from 'vconsole'
	import tokensData from '../tokens.js'
	import {Token} from '@mdex/heco-sdk'
	var _vConsole = new vConsole();
	export default {
		data() {
			return {
				token_items:tokensData.tokens,
				search_input:"",
				dialogVisible:false,
				Connect_dialogTableVisible:false,
				exchange_disabled:false,
				token_01:"",
				error: "",
				gasPrice: 0,
				gas: 0,
				userAgent: "",
				from:{
					address:"0x0000000000000000000000000000000000000000",
					symbol:"HT",
					decimals:18,
					chainId:128,
					logoURI:"https://cdn.jsdelivr.net/gh/T3COIN/test@main/resources/img/ht.png",
					input:"",
					balance:"-"
				},
				to:{
					address:"0xB141503a1189d3Af7060F3F14Ac7D37d3d70348E",
					symbol:"T3",
					decimals:18,
					chainId:128,
					logoURI:"https://cdn.jsdelivr.net/gh/T3COIN/test@main/resources/img/t3c.png",
					input:"",
					balance:"-"
				},
				route_path:[]
			}
		},
		created() {
			this.userAgent = window.navigator.userAgent
			var temp = this
			setInterval(function(){
				if(temp.from.address.length == 42 && temp.wallet.address.length == 42){
					temp.wallet.balanceOf(temp.from.address,temp.wallet.address,function(ret){
						temp.from.balance = Math.floor(ret * 1000000) / 1000000
					})
				}
				if(temp.to.address.length == 42 && temp.wallet.address.length == 42){
					temp.wallet.balanceOf(temp.to.address,temp.wallet.address,function(ret){
						temp.to.balance = Math.floor(ret * 1000000) / 1000000
					})
				}
				if(temp.wallet.address.length != 42){
					temp.from.balance="-"
					temp.to.balance="-"
				}
			},3000)
		},
		methods: {
			connectMetamask_click(){
				this.Connect_dialogTableVisible = false
				this.wallet.Metamask_Connect()
			},
			choiceTokenPage(e){
				console.log(e)
				this.token_01 = e
				this.dialogVisible = true
			},
			choiceToken(e){
				this.dialogVisible = false
				var temp = this
				if(this.token_01 == "from"){
					if(this.to.symbol == e){
						this.fe()
					}else{
						for(var i=0;i<this.token_items.length;i++){
							if(this.token_items[i].symbol == e){
								this[this.token_01].address = this.token_items[i].address
								this[this.token_01].symbol = this.token_items[i].symbol
								this[this.token_01].decimals = this.token_items[i].decimals
								this[this.token_01].logoURI = this.token_items[i].logoURI
								this[this.token_01].chainId = this.token_items[i].chainId
								break
							}
						}
					}
				}else{
					if(this.from.symbol == e){
						this.fe()
					}else{
						for(var i=0;i<this.token_items.length;i++){
							if(this.token_items[i].symbol == e){
								this[this.token_01].address = this.token_items[i].address
								this[this.token_01].symbol = this.token_items[i].symbol
								this[this.token_01].decimals = this.token_items[i].decimals
								this[this.token_01].logoURI = this.token_items[i].logoURI
								this[this.token_01].chainId = this.token_items[i].chainId
								break
							}
						}
					}
				}
			},
			max(){
				if(this.from.balance != "-" && this.from.balance > 0.01){
					this.from.input = (this.from.balance-0.001)+""
					this.from_input()
				}
			},
			fe(){
				var m = this.from
				this.from = this.to
				this.to = m
				if(this.from.symbol == "T3"){
					this.exchange_disabled = true
					this.$notify({
					  title: this.language[this.language.lang].Tips,
					  message: this.language[this.language.lang].T3_is_not_listed_and_cannot_be_traded,
					  type: 'warning'
					});
				}else{
					this.exchange_disabled = false
				}
			},
			to_input(){
				this.from.input = this.to.input / 10000
			},
			from_input(){
				this.to.input = this.from.input * 10000
			},
			but_approve(){
				
			},
			transact() {
				if(parseFloat(this.from.balance)-0.001<parseFloat(this.from.input)){
					this.$notify({
						title: this.language[this.language.lang].Tips,
						message: this.language[this.language.lang].Your_wallet_doesnt_have_enough_balance,
						type: 'warning'
					});
					return
				}
				this.wallet.swap({value:this.from.input,fromAddress:this.wallet.address})
			}
		}
	}
</script>
<style>
	.CSs54f8 > .el-input__inner {
		outline: none;
		border: none;
		font-size: 24px;
		color: rgb(0, 0, 0);
		padding: 0px;
	}
</style>
<style scoped>
	.ms15f {
		width: 100%;
		border-radius: 10px;
		font-weight: 500;
		padding: 18px;
		font-size: 20px;
		background-color: rgb(64, 158, 255);
		border: 1px solid transparent;
		font-weight: bold;
		color: #FFF;
	}

	.ms15f:hover {
		background-color: rgb(64, 148, 245);
	}
</style>
